<template>
<div>
   <section class="head">
      <div> <a @click="goBack" class="iconfont back"> &#xe614; </a> </div>
      <h1>Reviews List</h1>
      <div class="chartWrap">
       <span class="bag" v-if="indexData.shoppingCartNum > 0"> <em class="font10"> {{indexData.shoppingCartNum}} </em> </span>
        <a href="javascript:void(0)" @click="$router.push('/cart')" class="iconfont cart"> &#xe669; </a></div>
    </section>
    <div class="scoreWrap">
        <div class="score">
            <div class="score_left">
                <span class="num">{{commentList.spuCommentHeadVo.avgPoints}}/5</span>
                <van-rate v-model="commentList.spuCommentHeadVo.avgPoints" :size="18" color="#e33946" void-icon="star" void-color="#999999" readonly/>
                <span class="count">{{commentList&&commentList.spuCommentHeadVo.commentSum}} Reviews</span>
            </div>
            <div class="score_right">
                <div>
                    <div class="starRating">
                        <span class="starClass">5</span>
                        <van-progress :percentage="commentList.spuCommentHeadVo.fivePoints*100/(commentList&&commentList.spuCommentHeadVo.commentSum*1)" :show-pivot="false" color="#e33946"/>
                        <span class="starRatingCount">{{commentList&&commentList.spuCommentHeadVo.fivePoints}}</span>
                    </div>
                    <div class="starRating">
                        <span class="starClass">4</span>
                        <van-progress :percentage="commentList.spuCommentHeadVo.fourPoints*100/(commentList&&commentList.spuCommentHeadVo.commentSum*1)" :show-pivot="false" color="#e33946"/>
                        <span class="starRatingCount">{{commentList&&commentList.spuCommentHeadVo.fourPoints}}</span>
                    </div>
                    <div class="starRating">
                        <span class="starClass">3</span>
                        <van-progress :percentage="commentList.spuCommentHeadVo.threePoints*100/(commentList&&commentList.spuCommentHeadVo.commentSum*1)" :show-pivot="false" color="#e33946"/>
                        <span class="starRatingCount">{{commentList&&commentList.spuCommentHeadVo.threePoints}}</span>
                    </div>
                    <div class="starRating">
                        <span class="starClass">2</span>
                        <van-progress :percentage="commentList.spuCommentHeadVo.twoPoints*100/(commentList&&commentList.spuCommentHeadVo.commentSum*1)" :show-pivot="false" color="#e33946"/>
                        <span class="starRatingCount">{{commentList&&commentList.spuCommentHeadVo.twoPoints}}</span>
                    </div>
                    <div class="starRating">
                        <span class="starClass">1</span>
                        <van-progress :percentage="commentList.spuCommentHeadVo.onePoints*100/(commentList&&commentList.spuCommentHeadVo.commentSum*1)" :show-pivot="false" color="#e33946"/>
                        <span class="starRatingCount">{{commentList&&commentList.spuCommentHeadVo.onePoints}}</span>
                    </div>
                </div>
            </div>
        </div>
    </div>
    <div class="commentWrap">
        <div class="comment">
            <!-- <div class="setmeal red">
                Lable 1
            </div> -->
                <div :class="{'setmeal':true,'red':labelIndex==index}" v-for='(item,index) in commentList.spuCommentHeadVo.labelList' :key="index" @click="chooseLabel(item,index)">
               {{item.name}}
            </div>
        </div>
    </div>
    <div class="evaluate">
        <ul>
            <li class="" v-for="(item,index) in commentList.spuCommentFootVos" :key="index">
                <van-cell :value=" StringToDate(item.commentTimeStr) ">
                    <template slot="title">
                        <span class="custom-text">{{item.nickName}}</span>
                        <van-rate v-model="item.points" :size="14" color="#e33946" void-icon="star" void-color="#999999" readonly/>
                    </template>
                </van-cell>
                <span class="speak">{{item.comment}}</span>
            </li>


        </ul>

    </div>
    <img class="addReviews" src="../common/images/add.png" @click='handlerReview'>
  </div>
</template>

<script>
import {getCommentList,judgeComment} from '@/api/product.js';
import {mapGetters} from 'vuex'
export default {
  name: 'reviewsList',
  data () {
    return {
     labelIndex:-1,
     spuCode:this.$route.query.spuCode,
     commentList:[]
    }
  },
  computed: {
      ...mapGetters(['indexData'])
  },
  mounted() {
    this.init();
  },
  methods: {
    init(){
       getCommentList({spuCode:this.spuCode}).then(res=>{
           this.commentList=res.data.result;
       })
    },
    changeSort(val){
      this.sortValue=val;
      this.sortVisible=false;
    },
    goBack(){
      this.$router.back(-1)
    },
    chooseLabel(label,index){
        this.labelIndex=index;
         getCommentList({spuCode:this.spuCode,labelId:label.id}).then(res=>{
           this.commentList=res.data.result;
       })
    },
    handlerReview(){
        judgeComment({
            spuCode:this.spuCode
        }).then(res=>{
            if(res.data.result.judge==1){
                this.$router.push({path:'reviewsOrder',query:{orderNo:res.data.result.orderNo}});
            }else{
                this.$toast("You can't add comment because you haven't bought this item.");
            }
        });
    },
    StringToDate(DateStr)  {   
      var converted = Date.parse(DateStr);  
      var myDate = new Date(converted);  
      if (isNaN(myDate))  
        {   
            //var delimCahar = DateStr.indexOf('/')!=-1?'/':'-';  
            var arys= DateStr.split('-');  
            myDate = new Date(arys[0],--arys[1],arys[2]);  
        }  
        var now = myDate;
        var year = now.getFullYear();       //年
        var month = now.getMonth() + 1;     //月
        var day = now.getDate();            //日
       
        var hh = now.getHours();            //时
        var mm = now.getMinutes();          //分
        var ss = now.getSeconds();          //秒

        if(day < 10)
            clock += "0";
           
        var clock = day + "/";

        if(month < 10)
            clock += "0";
       
        clock += month + "/";

        clock += year + " ";

        return(clock); 

    } 

  }
}
</script>

<style lang="scss" scoped>
   .scoreWrap{
       width: 100%;
       height: 134px;
       padding-top: 10px;
        background:rgba(246,246,246,1);

   }
//    .scoreWrap /deep/ .van-rate__icon{
//        color: #e33946 !important;
//    }
   .score{
       width: 100%;
       padding: 0 4%;
       background-color:  white;
       height: 100%;
       display: -webkit-box;
       .score_right{
            .starRating{
                display: flex; align-items: center; justify-content: center;
                padding: 3px 0;
                .van-progress{
                    height: 6px;
                    width: 149px;
                    margin: 0 13px 0 5px;
                    color: #e33946;
                }
                .starClass{
                    display: block;
                    color: #666666;
                }
                .starRatingCount{
                    display: block;
                    color: #999999;
                }
            }
       }
   }
   .score_left{
       height: 100%;
       width: 35%;
   }
   .num{
       font-size: 5vw;
       color: #E33946;
       padding-left: 5px;
       display: block;
       width: 100%;
       font-size: 20px;
       font-weight: 500;
       padding: 15px 0 9px 5px;
   }
   .count{
        color:rgba(102,102,102,1);
        padding-left: 5px;
        font-size: 12px;
        padding-top: 9px;
   }
   .score_right{
        height: 100%;
        width: 65%;
        display: flex;
        align-items: center;
        justify-content: flex-end;
   }
   .commentWrap{
       padding-top: 10px;
       background:rgba(246,246,246,1);
   }
   .comment{
      width: 100%;
      padding: 10px 4% 0 4%;
      height: 97px;
      background-color: white;
    }
     .setmeal{
        width: 91px;
        height: 27px;
        font-size: 14px;
        line-height: 26px;
        border: #666666 1px solid;
        border-radius: 2px;
        text-align: center;
        color: #666666;
        float: left;
        margin-top: 10px;
        margin-right: 24px;
    }
    .red{
      border: #E33946 1px solid;
      color: #E33946;
    }
    .evaluate{
        background:rgba(246,246,246,1);
        padding-top: 10px;
        ul{
            background-color: white;
        }
        li{
            padding: 0 4%;
           .van-cell__title{
             display: -webkit-box;
           }
          .van-cell__value{ font-size: 12px;}
           .van-cell{
             padding: 10px 0 0 0 ;
           }
           .van-cell:not(:last-child)::after{
             border-bottom: none;
           }
          .van-rate{margin-left: 10px;}
        }
    }



    .speak{
        width: 100%;
        border-bottom: 1px solid #FFF6F6F6;
        display: block;
        font-size: 14px;
        padding: 10px 0;
    }
    .addReviews{
        position: fixed;
        right: 15px;
        bottom: 20px;
        width: 40px;
    }
</style>



